@import "color.scss";

nav{

    margin:0;
    height:64px;
    width: 100%;
    background-color: lightblue;
    display:flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    z-index: 1;

    & > a {
        display: flex;
        align-items: center;
        color: black;
        text-decoration: none;
        gap: 10px;

        svg {
            font-size: 40px;
        }
    }

    ul{
        list-style: none;
        display: flex;
        justify-content: space-around;
        align-items: center;

        li{
            margin: 0 5px;

            a{
                text-decoration: none;
                color: black;
                
                padding:4px;
            }

            &:nth-of-type(1){
                margin-right: 40px;
            }

            &:nth-of-type(5){
                margin-left: 40px;
            }
        }
    }

}