@use "./color.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-x: hidden;

  background-color: rgb(238, 251, 255);


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page {
  padding-top: 64px;
  min-height: 100vh;
  box-sizing: border-box;
}

.page-form{
  padding-top: 104px;
  min-height: 100vh;
  box-sizing: border-box;
  display:flex;
  align-items: center;
  justify-content:center;
  flex-direction: column;
  box-sizing: border-box;
  gap: 30px;
  padding-bottom: 40px;

}

.btn {
  cursor:pointer;
  border-radius: 6px;
  border: none;
  padding: 10px;
  background-color: rgb(160, 212, 212);
  color: black;
  font-weight: 600;
  text-decoration: none !important;
  box-sizing: border-box;

  &:hover{
    background-color:rgb(98, 167, 167);
  }

  &-half{
    width:50%;
  }
  
  &-red {
    background-color: rgb(235, 81, 81);
  }

  &-grey {
    background-color: rgb(202, 202, 202);
  }

  &-select {
    background-color: white;
    color: rgb(160, 212, 212);
    
  }

  &-active{
    background-color:rgb(98, 167, 167);
  }

  border: solid 2px rgb(160, 212, 212);
  
}


form{

  padding:40px 20px;
  gap: 40px;
  display:flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  width: calc(100vw - 40px);
  max-width: 900px;
  background-color: white;
  border-radius: 6px;
  box-shadow: rgba(100, 100, 111, 0.089) 0px 7px 29px 0px;
  h2{
    margin:0;
  }

  svg {
    font-size: 100px;
    color: rgb(160, 212, 212);
  }

  section{
    display:flex;
    flex-direction:column;
    align-items: center;
    gap:20px;
    height:60%;
    width:100%;

    & > div.flex-center{
      justify-content: center;
    }

    & > div.flex-start{
      justify-content: flex-start;
    }

    & > div{
      width:80%;
      display: flex;
      justify-content: space-between;
      height:100%;
      gap:40px;

      align-items: center;

      input{
        width:68%;
        height:60%;
        font-size: 1.1em;
        border:none;
        outline: none;
        border-bottom: 2px black solid;
        transition: all 0.2s ease-in-out;
        
        &:focus{
          border-bottom: 2px rgb(174, 168, 255) solid;
        }
      }

      input[type='checkbox']{
        width:auto;
      }

      select{
        width:68%;
        height:60%;
      }

      label{
        font-size: 1.1em;
        width: max-content !important;
      }
      
    }
  }
}

.page-form-info {

  section{

    & > div{

    input {
      border-bottom: none !important;
    }
  }
}
  
}