    background-color: rgb(217, 245, 255);
    height: 350px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    h1{
        color:black;
        text-align: center;
        font-size:3em;
        margin:0;
        
