.presentation{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height:450px;

    div{
        width:45%;
        height:80%;
        background: rgb(243, 252, 255);
        box-shadow: rgba(0, 0, 0, 0.13) 1px 1px 5px;
        display:flex;
        flex-direction: column;
        align-items: center;
        h2{
            text-align:center;
            color:rgb(63, 63, 63);
        }
        p{
            text-align: justify;
            text-justify: inter-word;
            font-size:1.3em;
            color:rgb(63, 63, 63);
            margin: 0 30px 0 30px;
        }
    }
}