.wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    padding-top: 30px;
    box-sizing: border-box;
    
}

.card {
    display: flex;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    font-weight: 500;

    p {
        color: black;
    }



    svg {
        font-size: 40px;
        color: rgb(77, 182, 182) !important;
    }

    &-verif {
        background-color: rgb(255, 218, 148);
    }
}

.buttons {
    display: flex;
    gap: 10px;
}

.head {
    padding: 30px 20px;
    
    box-sizing: border-box;
}

